}



// Bootstrap Overrides
.text-primary {
    color: $theme-primary;
}

.no-gutter > [class*='col-'] {
    padding-right:0;
    padding-left:0;
}

.small-icon {
    margin-bottom: 15px; /* Adjust this value as needed */
}

// Button Styles
.btn-default {
    @include button-variant($theme-dark, white, white);
}

.btn-primary {
    @include button-variant(white, $theme-primary, $theme-primary);
}

.btn {
    @include sans-serif-font;
    border: none;
    border-radius: 300px;
    font-weight: 700;
    text-transform: uppercase;
}

.btn-xl {
    padding: 15px 30px;
}

// Contact
#contact .fa {
    color: $theme-dark;
    font-size: 4em;
}


// Aside
/* Give the section a minimum height and use flexbox to organize its content */
.aside-container {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
}

/* Push the copyright to the bottom */
.copyright-container {
    margin-top: auto;
    padding-bottom: 15px;  /* Some padding to keep it away from the absolute bottom */
}

/* Adjust the copyright text size */
.copyright-text {
    font-size: 1.1em;  /* Adjust this value as needed */
}

